<template>
  <div id="pieData1"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {

  },

  methods: {
    init(s,z) {
      var v = [];
      v.push(
        { name: "胜",value:s},
        { name: "总体",value: z},
      );
      this.loadChart(v);
    },
    loadChart(data) {
      this.myChart = echarts.init(document.getElementById("pieData1"));
      this.myChart.setOption(this.initOption(data));
    },
    initOption(data) {
      return {
        color: ["#69ffd7", "#3269d9"],
        series: [
          {
            name: "访问来源",
            type: "pie",
            radius: ["50%", "70%"],
            avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: "center"
              },
              emphasis: {
                show: true,
                textStyle: {
                  fontSize: "30",
                  fontWeight: "bold"
                }
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            center: ["50%", "60%"], 
            data: data
            // [
            //   { value: 335, name: "命中" },
            //   { value: 310, name: "未命中" }
            // ]
          }
        ]
      };
    }
  }
};
</script>
