<template>
  <section class="data_section" ref="data_section">
    <h3 class="aTitle">攻击能力分析</h3>
    <div class="headerData">
      <el-select filterable v-model="uid" @change="selectGet" placeholder="请选择人员">
        <el-option
          v-for="item in options"
          v-if="item.mode !='1'"
          :key="item._id"
          :label="item.name"
          :value="item._id"
        ></el-option>
      </el-select>
      <el-date-picker
        class="dateWrapper"
        v-model="value1"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-button type="primary" @click="search(4)" class="search" icon="el-icon-search">查询</el-button>
    </div>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="12" class="searchWrapper">
        <el-button class="but1 search" type="primary" @click="search(2)">总体数据</el-button>
        <el-button type="primary" class="search" @click="search(0)">前仓数据</el-button>
        <el-button type="primary" class="search" @click="search(1)">后仓数据</el-button>
      </el-col>
      <el-col :span="6">&nbsp</el-col>
      <el-col :span="6">
        <ul class="df">
          <li>
            <p>总得分：</p>
            <p>{{zdf}}</p>
          </li>
          <li>
            <p>局平均得分:</p>
            <p>{{jdf}}</p>
          </li>
        </ul>
      </el-col>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft">
        <p class="aTopLeftTitle">对抗次数和胜率</p>
        <div>
          <pie1 ref="pie1" class="tj"></pie1>
          <pie2 ref="pie2" class="tj"></pie2>
          <pie3 ref="pie3" class="tj"></pie3>
        </div>
        <span>
          <p>
            <span>胜</span>
            <br />
            <span>{{vs.s}}%</span>
          </p>
          <p>
            <span>负</span>
            <br />
            <span>{{vs.f}}%</span>
          </p>
          <p>
            <span>平</span>
            <br />
            <span>{{vs.p}}%</span>
          </p>
        </span>
      </div>
      <div class="aTopRight">
