<script>
import { aAnalysis } from "@/api/a";
import pie1 from "./components/pie1";
import pie2 from "./components/pie2";
import pie3 from "./components/pie3";
import barY from "./components/barY";
import bottomPie1 from "./components/bottomPie1";
import bottomPie2 from "./components/bottomPie2";
import bottomPie3 from "./components/bottomPie3";
import bottomPie4 from "./components/bottomPie4";
export default {
  components: {
    pie1,
    pie2,
    pie3,
    bottomPie1,
    bottomPie2,
    bottomPie3,
    bottomPie4,
    barY
  },
  data() {
    return {
      heightBL: "50%",
      topBL: "50%",
      options: this.sessionData("get", "userList"),
      user: this.sessionData("get", "user"),
      value1: "",
      vs: {
        s: 0,
        f: 0,
        p: 0
      },
      weapon: {
        10: 0,
        12: 0,
        15: 0,
        255: 0,
        zm: 0,
        zw: 0
      },
      zdf: 0,
      name: "",
      uid: "",
      site: "1",
      jdf: 0
    };
  },
  created() {},
  mounted() {
    this.aAnalysis(this.user.name, this.site);
  },
  methods: {
    search(s) {
      s = s == 4 ? this.site : s;
      var st = this.value1 ? this.value1[0].getTime() : "";
      var et = this.value1 ? this.value1[1].getTime() : "";
      this.aAnalysis(this.name, s, st, et);
    },
    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.name = obj.name;
    },
    aAnalysis(name, site, start, end) {
      aAnalysis({
        name: name || this.user.name,
        site: site,
        start: start || "",
        end: end || ""
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          console.log("攻击力能力数据",list);
          this.zdf = list.score;
          this.jdf = list.scoreavg.toFixed(2);
          this.setMW(list.weapon);
          this.setVS(list.winner, list.loser, list.tier);
          this.$refs.barY.init(list.distance);
          this.$refs.bottomPie1.init(list.cause[255]);
          this.$refs.bottomPie2.init(list.cause[10]);
          this.$refs.bottomPie3.init(list.cause[12]);
          this.$refs.bottomPie4.init(list.cause[15]);
        }
      });
    },

    setVS(s, f, p) {
      var z = s + f + p;

      var s1 = (s / z).toFixed(2);
      var f1 = (f / z).toFixed(2);
      var p1 = (p / z).toFixed(2);
      this.vs.s = isNaN(s1) ? 0 : Number(s1) * 100;
      this.vs.f = isNaN(f1) ? 0 : Number(f1) * 100;
      this.vs.p = isNaN(p1) ? 0 : Number(p1) * 100;

      this.$refs.pie1.init(s, f + p);
      this.$refs.pie2.init(f, s + p);
      this.$refs.pie3.init(p, s + f);
    },

    setMW(data) {
      var zm = 0;
      var zw = 0;

      for (var i in data) {
        var m = data[i][0];
        var w = data[i][1];
        zm += m;
        zw += w;
        this.weapon[i] = ((m / (m + w)) * 100).toFixed(2);
        this.weapon[i] = isNaN(this.weapon[i])
          ? Number(0)
          : Number(this.weapon[i]);
      }
      this.weapon["zm"] = Number(((zm / (zm + zw)) * 100).toFixed(0));
      this.weapon["zw"] = Number(((zw / (zm + zw)) * 100).toFixed(0));
      this.weapon["zm"] = isNaN(this.weapon["zm"])
        ? Number(0)
        : this.weapon["zm"];
      this.weapon["zw"] = isNaN(this.weapon["zw"])
        ? Number(0)
        : this.weapon["zw"];
    }
  }
};
</script>

